import { Component, OnInit } from '@angular/core';
import { UsuarioService } from '../../services/usuario/usuario.service';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.sass']
})
export class DashboardComponent implements OnInit {

  graficas = []
  grafica:any  = {};

  constructor(
    private us:UsuarioService,
  ) { }

  ngOnInit(): void {
    this.getGraficas()
  }

  async getGraficas(){
    try{

      let graficas = await this.us.getGraficasUsuario();
      this.graficas = graficas.map( (a,i ) =>{
        a.porcentaje = 0;
        a.active     = i === 0;
        return a
      });
      this.grafica = this.graficas[0];

    }catch(err){
      throw err;
    }
  }

  handleChangePercentage(grafica){
    this.grafica = this.graficas.find(g=>g.idGrafica === grafica.idGrafica);
    this.graficas.forEach(g=> g.active = g.idGrafica === grafica.idGrafica )
  }
}
